@import '../../global.scss';


.menu{
    width: 300px;
    height: 100vh;
    background-color: $mainColor;
    position: fixed;
    top: 0;
    right: -300px;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 1s ease;
    display: flex;
    flex-direction: column;

    .menuIconWrap{
        width: 100%;
        display: flex;
        justify-content: space-evenly;
        margin-bottom: 20px;

        .menuIcon{
            font-size: 28px;
            color: #fff;
        }
    }

    &.active{
        right: 0;
    }

    ul{
        margin: 0;
        padding: 0;
        list-style: none;
        font-size: 35px;
        font-weight: 200;
        color: white;
        width: 70%;

        li{
            margin-bottom: 10px;

            a{
               font-size: inherit;
               color: inherit;
               text-decoration: none; 
            }

            &:hover{
                font-weight: 600;
            }
        }
    }
}